.actiongrid{
    display: -ms-flexbox;
    display: flex;
    position: fixed;
    height: 56px;
    width: inherit;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 15px 5px 0px 0px;
}

@media(max-width: 650px){
    .actiongrid{
        display: grid;
    }
}


